"use client";
import { motion } from "framer-motion";

type Props = {
  height?: number;     // responsive için max yükseklik
};

const nodes = [
  { x: 70,  y: 90 },  { x: 120, y: 60 }, { x: 180, y: 80 }, { x: 230, y: 55 },
  { x: 280, y: 85 },  { x: 330, y: 120 },{ x: 270, y: 150},{ x: 210, y: 140 },
  { x: 150, y: 130 }, { x: 110, y: 170},{ x: 160, y: 195},{ x: 220, y: 205 },
  { x: 295, y: 190 },
];

const links = [
  [0,1],[1,2],[2,3],[3,4],[4,5],[2,7],[7,8],[8,1],[8,9],[9,10],[10,11],[11,12],[7,10],[5,12]
];

export default function BrainAnim({ height = 380 }: Props) {
  return (
    <div className="relative select-none">
      {/* sahne parıltısı */}
      <div className="absolute inset-0 -z-10 brain-scene" aria-hidden />
      {/* hafif partiküller */}
      {[...Array(18)].map((_, i) => (
        <motion.span
          key={i}
          className="absolute block rounded-full bg-emerald-400/30 blur-[2px]"
          style={{
            width: 3, height: 3,
            left: `${(i * 37) % 100}%`,
            top: `${(i * 19) % 100}%`,
          }}
          initial={{ opacity: 0, y: 0 }}
          animate={{ opacity: [0.1, 0.7, 0.1], y: [0, -8, 0] }}
          transition={{ duration: 3 + (i % 5) * 0.5, repeat: Infinity, delay: i * 0.12 }}
          aria-hidden
        />
      ))}

      <motion.svg
        viewBox="0 0 380 260"
        className="w-full"
        style={{ maxHeight: height }}
        initial={{ opacity: 0, scale: 0.96 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.6 }}
      >
        {/* degradeler */}
        <defs>
          <radialGradient id="bgGlow" cx="50%" cy="40%" r="60%">
            <stop offset="0%"  stopColor="rgba(20,184,166,0.55)" />
            <stop offset="60%" stopColor="rgba(16,185,129,0.15)" />
            <stop offset="100%" stopColor="rgba(0,0,0,0)" />
          </radialGradient>

          <linearGradient id="linkGrad" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%"   stopColor="#34d399" />
            <stop offset="50%"  stopColor="#22c55e" />
            <stop offset="100%" stopColor="#60a5fa" />
          </linearGradient>

          <filter id="glow" x="-50%" y="-50%" width="200%" height="200%">
            <feGaussianBlur stdDeviation="2" result="b"/>
            <feMerge><feMergeNode in="b"/><feMergeNode in="SourceGraphic"/></feMerge>
          </filter>
        </defs>

        {/* sahne arka parıltısı */}
        <rect x="0" y="0" width="380" height="260" fill="url(#bgGlow)" />

        {/* dış kontur (kesik çizgi, akış animasyonu) */}
        <motion.path
          d="M95,110 C80,70 135,35 175,50 C195,25 245,35 255,75 C290,70 320,105 305,140 C330,170 300,215 260,205 C235,230 185,230 165,205 C140,220 105,205 110,175 C80,165 75,135 95,110 Z"
          fill="none"
          stroke="rgba(99,102,241,0.9)" /* indigo vurgulu */
          strokeWidth="2"
          strokeLinecap="round"
          strokeDasharray="6 10"
          filter="url(#glow)"
          initial={{ strokeDashoffset: 0 }}
          animate={{ strokeDashoffset: [0, -180] }}
          transition={{ duration: 5, repeat: Infinity, ease: "linear" }}
        />

        {/* bağlantılar (renk geçişli, nabız) */}
        {links.map(([a,b], i) => {
          const A = nodes[a], B = nodes[b];
          return (
            <motion.line
              key={i}
              x1={A.x} y1={A.y} x2={B.x} y2={B.y}
              stroke="url(#linkGrad)"
              strokeWidth="2"
              strokeLinecap="round"
              initial={{ opacity: 0.25 }}
              animate={{ opacity: [0.25, 0.95, 0.25] }}
              transition={{ duration: 2.2, repeat: Infinity, delay: i * 0.12 }}
              filter="url(#glow)"
            />
          );
        })}

        {/* nöronlar (çok renkli parıltı) */}
        {nodes.map((n, i) => (
          <motion.circle
            key={i}
            cx={n.x}
            cy={n.y}
            r="5"
            fill={i % 3 === 0 ? "#34d399" : i % 3 === 1 ? "#60a5fa" : "#f59e0b"} /* emerald/blue/amber */
            filter="url(#glow)"
            initial={{ scale: 0.8, opacity: 0.7 }}
            animate={{ scale: [0.8, 1.22, 0.8], opacity: [0.7, 1, 0.7] }}
            transition={{ duration: 1.8, repeat: Infinity, delay: i * 0.06 }}
          />
        ))}
      </motion.svg>
    </div>
  );
}
